













export default {
  data: function() {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    onSubmit:function() {
      const formData = {
        email: this.email,
        password: this.password,
      }
      console.log(formData)
      this.$store.dispatch('login', {email: formData.email, password: formData.password})
    }
  }
}



